@use '../base/mixins' as mixin;

@import '../blocks/_get-started';
@import '../blocks/_review';

.section-case-item {
  padding-bottom: 0;
}

p {
  margin: 0;
}

.case-item {
  margin: 0 auto;
  max-width: 840px;
  display: flex;
  flex-direction: column;
  align-items: stretch;

  .h1-custom {
    margin-bottom: 32px;

    @include mixin.small-laptop {
      margin-bottom: 16px;
    }
  }

  &__subtitle {
    font-size: 18px;
    margin-bottom: 32px;

    @include mixin.small-laptop {
      font-size: 14px;
    }

    @include mixin.phone {
      margin-bottom: 24px;
    }
  }

  &__banner {
    text-align: center;
    margin-bottom: 40px;

    @include mixin.small-laptop {
      margin-bottom: 24px;
    }

    @include mixin.phone {
      margin-bottom: 16px;
    }

    span {
      font-size: 12px;

      a {
        font-size: 12px;
      }
    }
  }

  &__img {
    width: 100%;
    aspect-ratio: 1.89;
    margin-bottom: 16px;
    max-height: 444px;
    overflow: hidden;
    border-radius: 32px;

    @include mixin.small-laptop {
      max-height: 359px;
    }

    @include mixin.phone {
      max-height: 152px;
      border-radius: 16px;
      margin-bottom: 8px;
    }

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .link {
    text-decoration: underline;
  }

  .h2-custom {
    margin-top: 32px;
    margin-bottom: 12px;

    @include mixin.small-laptop {
      margin-top: 24px;
    }

    @include mixin.phone {
      margin-top: 16px;
    }
  }

  p {
    margin-bottom: 32px;

    @include mixin.small-laptop {
      margin-bottom: 24px;
    }

    @include mixin.phone {
      margin-bottom: 16px;
    }
  }

  .review {
    margin: 32px 0;

    @include mixin.small-laptop {
      margin: 24px 0;
    }
  }
}